<template>
  <BaseLayout title="账单">
    <template #extra>
      <van-button icon="cash-back-record" :style="{width:'110px', borderRadius:'10px 0 0 10px', color: '#000'}" color="#F6F6F6" @click="onCharge">划拨积分</van-button>
    </template>

    <van-dropdown-menu>
      <van-dropdown-item v-model="state.type" :options="state.option" @change="onRefresh" />
      <van-dropdown-item ref="choose" :title="state.title" @closed="onRefresh">
        <van-datetime-picker v-model="currentDate" type="year-month" title="选择年月" :min-date="minDate" :max-date="maxDate" @cancel="onCancel" @confirm="onConfirm" />
      </van-dropdown-item>
    </van-dropdown-menu>

    <van-pull-refresh v-model="state.refreshing" @refresh="onRefresh">
      <van-list class="list" v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="onLoad">
        <van-cell-group class="item" v-for="(item, index) in state.list" :key="index" :border="false">
          <van-cell class="line1" :title="item.remark">
            <span :style="{color: item.color}">{{item.amount}}</span>
          </van-cell>
          <van-cell class="line2" :title="item.dt" :value="'余额 ' + item.subtotal" :border="false"></van-cell>
          <van-icon class="icon" :name="item.icon" :color="item.color"></van-icon>
        </van-cell-group>
        <van-empty v-show="state.list.length == 0" :style="{width:'100%'}"></van-empty>
      </van-list>
    </van-pull-refresh>
  </BaseLayout>
</template>

<style scoped>
.list {
  min-height: 500px;
}
.item {
  position: relative;
  height: 90px;
  margin-top: 5px;
  padding-left: 40px;
  background: #FFF;
  overflow:hidden;
}
.line1 {
  font-size: 14px;
}
.line2 {
  color: #999;
  font-size: 12px;
}
.icon {
  position: relative;
  top: -60px;
  left: -30px;
  font-size: 32px;
}
</style>

<script>
import {ref, reactive} from 'vue'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  setup() {
    const choose = ref(null);
    const currentDate = ref(new Date());

    const state = reactive({
      refreshing: false,
      loading: false,
      finished: false,

      option: [
        {text: "全部类型", value: 0},
        {text: "充值", value: 1},
        {text: "积分变动", value: 2},
      ],
      type: 0,
      title: "选择年月",
      ts: 0,

      list: [],
      offset: 0,
      limit : 20
    });

    const onCancel = () => {
      state.title = "选择年月";
      state.ts = 0;
      choose.value.toggle();
    };
    const onConfirm = (value) => {
      state.title = new Date(value).getFullYear() + "年 " + (new Date(value).getMonth() + 1) + " 月";
      state.ts = new Date(value).getTime() / 1000;
      choose.value.toggle();
    };
    

    return {
      choose, 
      currentDate,
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2030, 11, 31),
      state,
      onCancel,
      onConfirm,
    };
  },

  created() {
    this.onRefresh();
  },

  methods: {
    onRefresh() {
      this.state.loading = true;
      this.state.finished = false;
      this.state.offset = 0;
      this.state.list = [];
      this.onLoad();
    },
    onLoad() {
      this.$http.get("/team/propertyDetail?type=" + this.state.type + "&ts=" + this.state.ts + "&offset=" + this.state.offset + "&limit=" + this.state.limit).then((data) => {
        this.state.refreshing = false;
        if (data.length == 0 || data.length < this.state.limit)
          this.state.finished = true;
        this.state.loading = false;
        this.state.offset += data.length;

        for (let i in data){
          let item = data[i];
          this.state.list.push({...item, ...{
            amount: item.cash > 0 ? item.cash : item.point,
            icon: item.cash > 0 ? "cash-on-deliver" : "cash-back-record",
            color: (item.cash > 0 || item.point > 0) ? "#090" : "#C00",
          }});
        }
      });
    },

  }
}
</script>
